import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import Users from './users';
import Contact from './contact';
import * as serviceWorker from './serviceWorker';
import { initializeFirebase , inicializarFirebase } from './push-notification';
import { Route, Link, BrowserRouter as Router } from 'react-router-dom'


const routing = (
    <Router>
      <div>
        <ul>
          <li>
            <Link to="/build">Home</Link>
          </li>
          <li>
            <Link to="/build/users">Users</Link>
          </li>
          <li>
            <Link to="/build/contact">Contact</Link>
          </li>
        </ul>
        <Route exact path="/build" component={App} />
        <Route path="/build/users" component={Users} />
        <Route path="/build/contact" component={Contact} />
      </div>
    </Router>
  )
  
  ReactDOM.render(routing, document.getElementById('root'))
//ReactDOM.render(<App />, document.getElementById('root'));
//ReactDOM.render(routing, document.getElementById('root'))



// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

initializeFirebase(); 
